body {
  font-family: "Noto Sans TC", sans-serif !important;
  
}



.navbar-outer {
  position: absolute;
  z-index: 20;
  left: 0px;
  top: 0px;

  width: 100%;
  height: 5rem;

  padding: 0.5rem 1rem 0 1rem;

  transition: 0.35s;

  background-color: transparent;
}

.navMove {
  background-color: #FFFFFF;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.3);

  position: fixed;

  transition: 0.35s;

  
}

.navbar-outer .navbar-inner{
  position: relative;

  width: 100%;
  height: 100%;

  background-color: transparent;
  
  display: flex;
  flex-direction: row;
  align-items: center;
}

.navbar-outer .navbar-inner .navbarBrand{
  width: 30%;
  height: 100%;

  margin-left: 1rem;
}

.navbar-outer .navbar-inner .navbarBrand .navbarBrand-inner {
  display: flex;
  justify-items: start;
  align-items: center;

  width: 100%;
  height: 100%;

  color: #f0f0f0;
}

.navbar-outer.navMove .navbar-inner .navbarBrand .navbarBrand-inner {
  display: flex;
  justify-items: start;
  align-items: center;

  width: 100%;
  height: 100%;

  color: #403e3e;
}

.navbar-outer .navbar-inner .navbarBrand .brandName {
  font-size: 1.5rem;
  letter-spacing: 0.3rem;
  font-weight: 300;

  margin: 0;
}

.navbar-outer .navbar-inner .navbarBrand .brandImg {
  height: 3rem;
  width: 3rem;
}

.navbar-outer .navbar-inner .nav .nav-link{
  color:#f0f0f0
}

.navbar-outer.navMove .navbar-inner .nav .nav-link{
  color:#403e3e;
}

.footer {
  width: 100%;
  height: 2rem;

