.publications-card {
  margin-top: 1rem;

  width: 100%;
  min-height: 15rem;

  transition: 0.25s;

  border-radius: 5px;
  /* border-color: black;
    border-style: solid;
    border-width: 1.1px; */

  background-color: transparent;
  color: #403E3E;
}

.publications-card:hover {
  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */

  background-color: #d0d0d0;
  color: #403E3E;

  transition: 0.25s;
}

.publications-card > .title {
  font-size: 1.8rem;
  letter-spacing: default;
  font-weight: 500;
  line-height: normal;

