.home .cover .cover-pc {
  position: relative;
  width: 100%;
  height: 100vh;

  display: flex;
  flex-direction: row;

  background-color: #212020;
}

.home .cover .cover-pc .title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  height: 100vh;

  margin-left: 15%;
}

.home .cover .cover-pc .title p {
  font-size: 4.5rem;
  font-weight: 200;
  color: #f0f0f0;
  opacity: 0.9;
}

.home .cover .cover-pc .graph {
  width: 70%;
  height: 100%;
  position: relative;
}

.home .cover .down-section {
  font-size: 3rem;
  color: #f0f0f0;
  opacity: 0.9;

  position: absolute;
  top: Calc(100vh - 5rem);
  left: 0;
  width: 100%;
  height: 5rem;

  display: flex;
  justify-content: center;
}



/* COVER MOBILE */
.home .cover .cover-mobile {
  position: relative;
  width: 100%;
  height: 100vh;
  z-index: 1;



  background-color: #212020;
}

.home .cover .cover-mobile .graph {
  width: 90%;
  height: 100%;
  position: relative;

  margin-left: auto;
  margin-right: auto;
}

.home .cover .cover-mobile .title {
  position: absolute;
  top: 0;
  left: 0;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;

  height: 100vh;
  width: 100%;
  z-index: 2;

  
}

.home .cover .cover-mobile .title p {
  font-size: 4.5rem;
  font-weight: 200;
  color: #f0f0f0;
  opacity: 0.9;
}

