/* component */
/* COVER */
.page .cover {
  position: relative;
  width: 100%;
  height: 100vh;

  background-color: #212020;

  overflow: hidden;
}

.page .cover .title {

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;


  height: 100%;
  width: 100%;

}

.page .cover .title p{
  font-size: 3.5rem;
  font-weight: 200;
  color: #f0f0f0;
  opacity: 0.9;

  margin-left: 10%;
}


.page .pageTitle .text{
  font-size: 4rem;
  font-weight: 300;
  color: #403e3e;

  display: flex;

  margin-top: 4rem;
}

.page .pageTitle .line {
  width: 100%;
  height: 4rem;

  border-top: 5px solid #ffa400;
}


.page .cover .mask {
  position: absolute;
  top: 90px;
  left: 50%;

	width: 100vw;
	height: Calc(100% - 90px);
	transform: skew(-20deg);
	background: #f0f0f0;
}

.page .cover .rec {
  position: absolute;
  bottom: 0;
  left: 0;

  display: block;
  width: 100%;
  height: 1vh;
  z-index: 2;
  background-color: #f0f0f0;
}

.page .cover .subtitle {
  position: absolute;
  top: 0;
  left: 50%;
  right: 0;
  bottom: 0;

  margin-right: 20%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}

.page .cover .subtitle p{
  font-size: 4rem;
  font-weight: 300;
  color: #403e3e;

  margin-top: 10%;
}
